const enlace = document.querySelectorAll(".navegacion-principal a");

document.addEventListener("DOMContentLoaded", () => {
  iniciarApp();
  iniciarSmootion();
  iniciarFixed();
});
function iniciarApp() {
  crearGaleria();
}

function iniciarFixed() {
  const barra = document.querySelector(".header");
  const sobreFestival = document.querySelector(".sobre-festival");
  const body = document.querySelector("body");
  window.addEventListener("scroll", () => {
    if (sobreFestival.getBoundingClientRect().bottom < 0) {
      barra.classList.add("fixed");
      body.classList.add("body-scroll");
    } else {
      barra.classList.remove("fixed");
      body.classList.remove("body-scroll");
    }
  });
}

function iniciarSmootion() {
  enlace.forEach((enla) => {
    enla.addEventListener("click", (e) => {
      e.preventDefault();

      const scroll = e.target.attributes.href.value;
      const seccion = document.querySelector(scroll);
      seccion.scrollIntoView({ behavior: "smooth" });
    });
  });
}

function crearGaleria() {
  const galeria = document.querySelector(".galeria__imagenes");

  for (let i = 1; i <= 12; i++) {
    const imagen = document.createElement("picture");
    imagen.innerHTML = `
    <source srcset="build/img/thumb/${i}.webp" type="image/webp" /> 
    <img  width="200"  height="300"loading="lazy"src="build/img/thumb/${i}.jpg"alt="imagen galeria"/>`;

    galeria.appendChild(imagen);
    imagen.onclick = () => mostrarGaleria(i);
  }
}

function mostrarGaleria(id) {
  const imagen = document.createElement("picture");
  imagen.innerHTML = `
  <source srcset="build/img/grande/${id}.webp" type="image/webp" /> 
  <img  width="200"  height="300"loading="lazy"src="build/img/grande/${id}.jpg"alt="imagen galeria"/>`;
  //crea el overlay con la imagen
  const overlay = document.createElement("DIV");
  overlay.appendChild(imagen);
  overlay.classList.add("overlay");

  //añadir boton para cerra ventana modal
  const cerrar = document.createElement("P");
  cerrar.textContent = "X";
  cerrar.classList.add("cerrarVentana");
  overlay.appendChild(cerrar);

  cerrar.onclick = () => {
    const body = document.querySelector("body");

    overlay.remove(overlay);
    body.classList.remove("fijar-scroll"); //quitando la clase de quitar scroll
  };
  //añadirlo al html
  const body = document.querySelector("body");

  body.appendChild(overlay);
  body.classList.add("fijar-scroll"); //fijando el scroll
  overlay.onclick = () => {
    const body = document.querySelector("body");
    body.classList.remove("fijar-scroll"); //fijando el scroll

    overlay.remove();
  };
}
